export const userSignInReducer=(state={}, action)=>{
    switch(action.type){
        case "USER_SIGN_IN_REQUEST":
            return {loading: true};
        case "USER_SIGN_IN_SUCCESS":
            return {loading: false, success: true, userInfo: action.payload};
        case "USER_SIGN_IN_FAIL":
            return {loading: false, success: false, errors: action.payload};
        case "USER_SIGN_OUT":
            return {};
        default:
            return state;
    }
}

export const userSignUpReducer=(state={}, action)=>{
    switch(action.type){
        case "USER_SIGN_UP_REQUEST":
            return {loading: true};
        case "USER_SIGN_UP_SUCCESS":
            return {loading: false, success: true, userInfo: action.payload};
        case "USER_SIGN_UP_FAIL":
            return {loading: false,  success: false, errors: action.payload};
        default:
            return state;
    }
}

export const userProfileReducer=(state={loading: true}, action)=>{
    switch(action.type){
        case "USER_PROFILE_REQUEST":
            return {loading: true};
        case "USER_PROFILE_SUCCESS":
            return {loading: false, success: true, profile: action.payload};
        case "USER_PROFILE_FAIL":
            return {loading: false, success: false, errors: action.payload};
        default:
            return state;
    }
}

export const profileUpdateReducer=(state={}, action)=>{
    switch(action.type){
        case "PROFILE_UPDATE_REQUEST":
            return {loadingupdate: true};
        case "PROFILE_UPDATE_SUCCESS":
            return {loadingupdate: false, successupdate: true, updateduser: action.payload};
        case "PROFILE_UPDATE_FAIL":
            return {loadingupdate: false, successupdate: false, errorsupdate: action.payload};
        default:
            return state;
    }
}