import axios from 'axios';

export const userSignIn=(email,password)=>async(dispatch)=>{
    dispatch({type: "USER_SIGN_IN__REQUEST"});
    try{
        const {data} = await axios.post('/api/user/signin', {email,password});
        dispatch({type: "USER_SIGN_IN_SUCCESS", payload: (data)});
        localStorage.setItem("userInfo", JSON.stringify(data));
    }catch(error){
        dispatch({type: "USER_SIGN_IN_FAIL", payload: (error.response && error.response.data.message ?
            error.response.data.message : error.message)});
    }
}

export const userSignOut=()=>async(dispatch)=>{
    dispatch({type: "USER_SIGN_OUT"});
    localStorage.removeItem("userInfo");
}

export const userSignUp=(name, email, password)=>async(dispatch)=>{
    dispatch({type: "USER_SIGN_UP_REQUEST", payload:{name, email, password}});
    try{
        const {data} = await axios.post('/api/user/signup', {name, email, password});
        dispatch({type: "USER_SIGN_UP_SUCCESS", payload: (data)});
        dispatch({type: "USER_SIGN_IN_SUCCESS", payload: (data)});
        localStorage.setItem("userInfo", JSON.stringify(data));
    }catch(error){
        dispatch({type: "USER_SIGN_UP_FAIL", payload: (error.response && error.response.data.message ?
            error.response.data.message : error.message)});
    }
}

export const userProfile=(userID)=>async(dispatch, getstate)=>{
    dispatch({type: "USER_PROFILE_REQUEST", payload: userID});
    const {userLogin: {userInfo}} = getstate();
    try{
        const {data} = await axios.get(`/api/user/${userID}`,
        { headers: {Authorization: `Bearer ${userInfo.token}`}});
        dispatch({type: "USER_PROFILE_SUCCESS", payload: data});
    }catch(error){
        dispatch({type: "USER_PROFILE_FAIL", payload: (error.response && error.response.data.message ?
            error.response.data.message : error.message)});
    }
}

export const updateProfile=(user)=>async(dispatch, getstate)=>{
    dispatch({type: "UPDATE_PROFILE_REQUEST", payload: user});
    const {userLogin: {userInfo}} = getstate();
    try{
        const {data} = await axios.put(`/api/user/update`, user, {
            headers: {Authorization: `Bearer ${userInfo.token}`}});
        dispatch({type: "UPDATE_PROFILE_SUCCESS", payload: data});
        dispatch({type: "USER_SIGN_IN_SUCCESS", payload: data});
        localStorage.setItem("userInfo", JSON.stringify(data));
    }catch(error){
        dispatch({type: "UPDATE_PROFILE_FAIL", payload: (error.response && error.response.data.message ?
            error.response.data.message : error.message)});
    }

}