import React,{useState, useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {userProfile, updateProfile} from '../../store/actions/user';
import {Link} from 'react-router-dom';
import {Alert, Button} from 'react-bootstrap';
import '../page.css';
import LoadingBox from '../util/LoadingBox';
import swal from 'sweetalert';
import MessageBox from '../util/MessageBox';


function Profile() {
    const dispatch = useDispatch();

    const [name, setname] = useState('');
    const [email, setemail] = useState('');
    const [password, setpassword] = useState('');
    const [confirmpwd, setconfirmpwd] = useState('')

    const userLogin = useSelector((state)=> state.userLogin);
    const {userInfo} = userLogin;
    const profileuser = useSelector((state)=> state.profileuser);
    const {loading, profile, errors, success} = profileuser;

    const profileupdate = useSelector((state)=> state.profileupdate);
    const {loadingupdate, successupdate, errorsupdate} = profileupdate;

    useEffect(()=>{
        if(!profile){
            dispatch(userProfile(userInfo._id));
        }else{
            setname(profile.data.name);
            setemail(profile.data.email);
        }
    }, [dispatch, userInfo._id, profile]);

    const handleSubmit=(e)=>{
        e.preventDefault();
        if(password !== confirmpwd){
            swal("Oops", "Passwords Mismatched", 'error');
        }else{
            dispatch(updateProfile({
                userID: userInfo._id, name, email, password
            }));
        }
    }
    
    return (
        <div>
            {loading ? (<LoadingBox></LoadingBox>) 
            : 
            (<div className='login-section'>
            <div className='login-container'>
                 <div>
                 <span className='login-title'>Your Profile</span>
                 </div>
                 <form className='login-form' onSubmit={handleSubmit}>
                 {loadingupdate && (<LoadingBox></LoadingBox>)}
                 {errorsupdate && (<MessageBox>{errorsupdate}</MessageBox>)}
                 {successupdate && (<Alert variant='success'>Profile updated</Alert>)}
                 <div className='form-group'>
                     <label className='txt-lbl'>Name</label>
                     <input 
                     type='text' 
                     className='txt-input'
                     value={name}
                     onChange={(e)=> setname(e.target.value)}/>
                     </div>
                     <div className='form-group'>
                     <label className='txt-lbl'>Email</label>
                     <input 
                     type='text' 
                     className='txt-input'
                     value={email}
                     onChange={(e)=> setemail(e.target.value)}/>
                     </div>
                     <div className='form-group'>
                     <label className='txt-lbl'>Password</label>
                     <input 
                     type='password' 
                     className='txt-input'
                     value={password}
                     onChange={(e)=> setpassword(e.target.value)}/>
                     </div>
                     <div className='form-group'>
                     <label className='txt-lbl'>Confirm Password</label>
                     <input 
                     type='password' 
                     className='txt-input'
                     value={confirmpwd}
                     onChange={(e)=> setconfirmpwd(e.target.value)}/>
                     </div>
                     <Button type="submit" variant='primary' size='md'>Update Account</Button>
                 </form>
             </div>
         </div>)}
        </div>
    )
}

export default Profile;
